import React, { useState, useEffect } from 'react';
import '../../css/ChatModal.css';
import chatpixel1 from '../../assets/chatpixel1.gif';
import chatpixel2 from '../../assets/chatpixel2.gif';
import chatpixel3 from '../../assets/chatpixel3.gif';
import chatpixel4 from '../../assets/chatpixel4.gif';
import chatpixel5 from '../../assets/chatpixel5.gif';

const initialItems = [
  {
    name: '',
    gif: chatpixel1,
    goal: '5kg ',
    progress: 30,
    currentStatus: '   ',
  },
  {
    name: '',
    gif: chatpixel2,
    goal: '5kg ',
    progress: 60,
    currentStatus: '  2kg    ',
  },
  {
    name: '',
    gif: chatpixel3,
    goal: ' ',
    progress: 100,
    currentStatus: '  1...',
  },
  {
    name: '     ',
    gif: chatpixel4,
    goal: '  ',
    progress: 20,
    currentStatus: '    ',
  },
  {
    name: '  ',
    gif: chatpixel5,
    goal: '  ',
    progress: 20,
    currentStatus: '   ? ',
  },
];

function ChatModal() {
  const [items, setItems] = useState(() => {
    const savedItems = localStorage.getItem('characterItems');
    return savedItems ? JSON.parse(savedItems) : initialItems;
  });
  const [newItem, setNewItem] = useState({ name: '', gif: '', goal: '', progress: 0, currentStatus: '' });
  const [isEditing, setIsEditing] = useState(false);
  const [currentEditIndex, setCurrentEditIndex] = useState(null);

  useEffect(() => {
    localStorage.setItem('characterItems', JSON.stringify(items));
  }, [items]);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setNewItem({ ...newItem, [name]: value });
  };

  const handleAddItem = () => {
    setItems([...items, newItem]);
    setNewItem({ name: '', gif: '', goal: '', progress: 0, currentStatus: '' });
  };

  const handleEditItem = (index) => {
    setNewItem(items[index]);
    setIsEditing(true);
    setCurrentEditIndex(index);
  };

  const handleUpdateItem = () => {
    const updatedItems = [...items];
    updatedItems[currentEditIndex] = newItem;
    setItems(updatedItems);
    setNewItem({ name: '', gif: '', goal: '', progress: 0, currentStatus: '' });
    setIsEditing(false);
    setCurrentEditIndex(null);
  };

  const handleDeleteItem = (index) => {
    if (window.confirm(' ?')) {
      const updatedItems = items.filter((_, i) => i !== index);
      setItems(updatedItems);
    }
  };

  const handleReset = () => {
    if (window.confirm(' ?')) {
      setItems(initialItems);
      localStorage.removeItem('characterItems');
    }
  };

  return (
    <div className="chat-content">
      <div className="character-list">
        {items.map((item, index) => (
          <div key={index} className="character-card">
            <img src={item.gif} alt={item.name} className="character-gif" />
            <div className="character-info">
              <h3>{item.name}</h3>
              <p><span className='goal-font'>: </span>{item.goal}</p>
              <p className="current-status"><span className='status-font'>:</span> {item.currentStatus}</p>
              <div className="progress-bar">
                <span className="progress-label">: {item.progress}%</span>
                <div className="progress" style={{ width: `${item.progress}%` }}></div>
              </div>
              <div className="button-group">
                <button className="character-card-button" onClick={() => handleEditItem(index)}></button>
                <button className="character-card-button" onClick={() => handleDeleteItem(index)}></button>
              </div>
            </div>
          </div>
        ))}
      </div>
      <div className="character-form">
        <input
          type="text"
          name="name"
          placeholder=""
          value={newItem.name}
          onChange={handleInputChange}
          className="character-form-input"
        />
        <input
          type="text"
          name="goal"
          placeholder=""
          value={newItem.goal}
          onChange={handleInputChange}
          className="character-form-input"
        />
        <input
          type="text"
          name="currentStatus"
          placeholder=" "
          value={newItem.currentStatus}
          onChange={handleInputChange}
          className="character-form-input"
        />
        <input
          type="number"
          name="progress"
          placeholder="  (%)"
          value={newItem.progress}
          onChange={handleInputChange}
          className="character-form-input"
        />
        <button className="character-form-button" onClick={isEditing ? handleUpdateItem : handleAddItem}>
          {isEditing ? '' : ''}
        </button>
        <button className="character-form-button reset-button" onClick={handleReset}>
          
        </button>
      </div>
    </div>
  );
}

export default ChatModal;
